/*toggle-icon*/
.toggle-icon {
    display: block;
    position: relative;
    width: 100%;
    margin: -2.2em auto 0 0.2em;
    transition: margin 600ms;
    cursor: pointer;
}

.toggle-icon .bar {
    color: #fff;
    display: block;
    width: 3.8em;
    height: 0.2em;
    margin: 0.9em;
    border-right: 2.8em solid #fff;
    box-sizing: border-box;
    transition-property: margin, border-right-color, box-shadow, -webkit-transform;
    transition-property: transform, margin, border-right-color, box-shadow;
    transition-property: transform, margin, border-right-color, box-shadow, -webkit-transform;
    transition-duration: 600ms;
}

.toggle-icon:hover .bar {
    width: 4.3em;
}

.toggle-icon .bar:nth-of-type(1) {
    -webkit-transform: rotate(-180deg);
    transform: rotate(-180deg);
}

/*pushed*/
.pushed .toggle-icon {
    margin: -3.2em 0 0 15px;
}

.pushed .bar:nth-of-type(1) {
    margin-left: 1.4em;

    border-left: 1.6em solid#fff;
    -webkit-transform: rotate(-137deg) translateY(-1.5em);
    transform: rotate(-137deg) translateY(-1.5em);
}

.pushed .bar:nth-of-type(2) {
    border-left: 1.6em solid#fff;
    margin-left: 0.5em;
    -webkit-transform: rotate(-42deg);
    transform: rotate(-42deg);
}

.pushed .toggle-icon:hover .bar:nth-of-type(1) {
    xborder-right-color: #e9b8b9;
    xbox-shadow: 0.4em 0 0.6em 0.1em rgba(205, 40, 44, 0.75);
}
.pushed .toggle-icon:hover .bar:nth-of-type(2) {
    xborder-right-color: #ddf1da;
    xbox-shadow: 0.4em 0 0.6em 0.1em rgba(83, 228, 68, 0.65);
}

