    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

/* nav component */
li.list-navbar {
    display: flex;
}

li.list-navbar a {
    padding: 50px;
    cursor: pointer;
    font-family: inherit;
    color: inherit;
    font-size: inherit;
    border-left: 1px solid #ccc;
}

li.list-navbar a:hover {
    color: #656769;
}

.nav-active-red {
    background: #e51b43;
    background: var(--red);
    color: #fff !important;
}
.nav-active-grey {
    background: rgb(29, 29, 29);
    background: var(--grey);
    color: #fff !important;
}

.nav-active-green {
    background: rgb(102, 218, 102);
    background: var(--greeny);
    color: #fff !important;
}

.nav-active-blue {
    background: #002f6c;
    background: var(--blue);
    color: #fff !important;
}

/* buttons */

/* buttons for home page */
.normal-button {
    padding: 10px;
    min-width: 200px;
    cursor: pointer;
    margin: 30px 0px 30px 0px;
    color: #fff;
    border-width: 0px;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    border-radius: 3px;
    font-weight: 600;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.normal-button:hover {
    opacity: 0.9;
}

/* container component */

.article {
    width: 350px;
}

/* for carousel */
.carousel {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 500px;
    width: auto;
    overflow: hidden;
}

.inner {
    white-space: pre-wrap;
    transition: -webkit-transform 200ms;
    transition: transform 200ms;
    transition: transform 200ms, -webkit-transform 200ms;
}

.carousel-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.carousel-nav {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: auto;
    font-family: Arial, Helvetica, sans-serif;
    padding: 0px 20px 0px 20px;
    justify-content: center;
}

.carousel-nav > button {
    border: 0px;
    background: white;
    cursor: pointer;
}

.carousel-nav > p {
    font-size: 50px;
    color: rgb(102, 218, 102) !important;
    color: var(--greeny) !important;
    font-weight: 1000;
}

.carousel-item p {
    font-size: 14px;
    width: 350px;
}

